<template>
<div style="width: 100%">
    <div class="pub-form-col-2" v-if="source.InvolvedPersons.length === 0">
        <span class="label">&nbsp;</span>
        <div class="content">
            <el-button class="pub-button-space pub-save-btn" size="mini" @click="addInvolved(index, 0)">添加涉案者</el-button>
        </div>
    </div>
    <div v-else :class="{'pub-form-item-child': true, 'last': idx === source.InvolvedPersons.length - 1}" v-for="(i, idx) in source.InvolvedPersons" :key="idx">
        <div class="pub-form-col-2">
            <span class="label">涉案者</span>
            <div class="content">
                <el-input v-model="i.InvolvedPerson" @blur="checkNameRepeat(i.InvolvedPerson, idx)" :class="{'danger': i.InvolvedPersonErr !== '' && i.InvolvedPersonErr !== undefined}" type="textarea" autosize :rows="1" placeholder="涉案者"></el-input>
                <el-tooltip class="item" effect="dark" :content="i.InvolvedPersonErr" placement="top-start">
                    <i class="errSpanMin el-icon-error" v-show="i.InvolvedPersonErr !== '' && i.InvolvedPersonErr !== undefined"></i>
                </el-tooltip>
            </div>
        </div>
        <div class="pub-form-col-2">
            <span class="label">涉案者类型</span>
            <div class="content content-box">
                <div class="left-content">
                    <el-select v-model="i.InvolvedPersonType" @change="checkRepeat($event, idx)" clearable filterable :class="{'danger': i.InvolvedPersonTypeErr !== '' && i.InvolvedPersonTypeErr !== undefined, 'w100': true}">
                    <el-option v-for="ii in dictObj['98']" :key="ii.DId" :label="ii.Title" :value="ii.DId"></el-option>
                    </el-select>
                </div>
                <div class="right-content right-content-icon">
                    <span v-if="idx > 1" class="pub-form-icon el-icon-remove remove-icon-color" @click="deleteInvolved(index, idx)"></span>
                    <span class="pub-form-icon el-icon-circle-plus add-icon-color" @click="addInvolved(index, idx)"></span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script lang="ts">
import { defineComponent ,inject, reactive, toRefs, onBeforeMount, ref } from 'vue'
export default defineComponent ({
    props: {
        source: {
            type: Object,
            default: () => {
                return {}
            }
        },
        dictObj: {
            type: Object,
            default: () => {
                return {}
            }
        },
        index: {
            type: Number,
            default: 0
        }
    },
    setup(props, context) {
        const $api :any = inject('$api')
        const addInvolved = (index:number,idx:number) => {
            props.source.InvolvedPersons.splice(idx+1, 0, { InvolvedPerson: '', InvolvedPersonType: '' })
        }
        const deleteInvolved = (index:number, idx : number) => {
            props.source.InvolvedPersons.splice(idx, 1);
        }
        const checkRepeat = (value: any, index: any) => {
            for(let i=0;i<props.source.InvolvedPersons.length;i++){
                if(props.source.InvolvedPersons[i].InvolvedPersonType === value && index !== i) {
                    props.source.InvolvedPersons[index].InvolvedPersonTypeErr = '涉案人类型不能重复'
                    break;
                } else {
                    props.source.InvolvedPersons[index].InvolvedPersonTypeErr = ''
                }
            }
        }
        const checkNameRepeat = (value: any, index: number) => {
            for(let i=0;i<props.source.InvolvedPersons.length;i++){
                if(props.source.InvolvedPersons[i].InvolvedPerson === value && index !== i) {
                    props.source.InvolvedPersons[index].InvolvedPersonErr = '录入涉案人相同，请再确认'
                    break;
                } else {
                    props.source.InvolvedPersons[index].InvolvedPersonErr = ''
                }
            }
        }
        return {
            addInvolved,
            deleteInvolved,
            checkRepeat,
            checkNameRepeat
        }
    }
})
</script>
